import React from 'react'
import ConstantTime from './ConstantTime'

const  VariableInterval= ({ intervals }) => {
	let testArray = []
	for (let i = 0; i < intervals; i++) {
		testArray.push(<ConstantTime key={i} index={i}/>)
	}

	return (
		<div>
			{testArray}
		</div>
	)
}

export default VariableInterval