import React from 'react'

const BreakInput = ({ breaksType, index }) => {
	let breakId
	if (breaksType === 'constant') {
		breakId = 'breakDuration'
	} else {
		breakId = `break${index}`
	}
	return (
		<div className="input-group input-group-lg w-25 center">
  			<div className="input-group-prepend">
    			<span className="input-group-text" id="inputGroup-sizing-lg">Enter Break Duration</span>
  			</div>
  			<input type="text" className="form-control" aria-label="Sizing example input" aria-describedby="inputGroup-sizing-lg" id={breakId}/>
		</div>
	)
}

export default BreakInput