import React, { Component } from 'react';
import { connect } from 'react-redux';
import useSound from 'use-sound';
import beep from './beep.mp3'
import SelectionScreen from './components/SelectionScreen'
import TimeInput from './components/TimeInput'
import ConstantDoneButton from './components/ConstantDoneButton'
import ConstantTime from './components/ConstantTime'
import ConstantTimeCountdown from './components/ConstantTimeCountdown'
import BreakChoice from './components/BreakChoice'
import BreakType from './components/BreakType'
import BreakInput from './components/BreakInput'
import VariableInterval from './components/VariableInterval'
import VariableDoneButton from './components/VariableDoneButton'
import VariableIntervalNext from './components/VariableIntervalNext'
import BreaksList from './components/BreaksList'
import beepDone from './beepDone.mp3'
import beepBreakDone from './beepBreakDone.mp3'
import './App.css';

import { setViewType, setTimerType, setConstantDuration, setIntervals, setCountdownStart, setBreaksCondition, setBreaksType, setBreakTime } from './actions'

const mapStateToProps = state => {
  return {
    viewType: state.updateViewType.viewType,
    timerType: state.updateTimerType.timerType,
    constantDuration: state.updateConstantDuration.constantDuration,
    intervals: state.updateIntervals.intervals,
    countdownStarted: state.updateCountdownStart.countdownStarted,
    breaksCondition: state.updateBreaksCondition.breaksCondition,
    breaksType: state.updateBreaksType.breaksType,
    breakTime: state.updateBreakTime.breakTime
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    setViewType: (viewType) => dispatch(setViewType(viewType)),
    setTimerType: (timerType) => dispatch(setTimerType(timerType)),
    setConstantDuration: (duration) => dispatch(setConstantDuration(duration)),
    setIntervals: (intervals) => dispatch(setIntervals(intervals)),
    setCountdownStart: (countdownStart) => dispatch(setCountdownStart(countdownStart)),
    setBreaksCondition: (breaksCondition) => dispatch(setBreaksCondition(breaksCondition)),
    setBreaksType: (breaksType) => dispatch(setBreaksType(breaksType)),
    setBreakTime: (breakTime) => dispatch(setBreakTime(breakTime))
  }
}

class App extends Component {

  constructor(props) {
    super(props) 
    this.state = {
      constantDurationState: 0,
      constantInterval: 0,
      constantDurationArray: [],
      breakTime: false,
      breakRoundCounter: 0,
      variableInterval: 0,
      variableDurationArray: [],
      firstVariableNext: false,
      doneSoundPlayed: false
    }
  }

  getConstantInfo = () => {
    const { setConstantDuration, setIntervals, constantDuration, timerType } = this.props
    let intervals = parseFloat(document.getElementById('intervals').value)
    let duration
    if (timerType === 'constant') {
      duration = parseFloat(document.getElementById('constantDuration').value)
      setConstantDuration(duration)
    }
    setIntervals(intervals)
    if (timerType === 'constant') {
      this.setState({ constantDurationState: duration})
      for (let i = 0; i < intervals; i++) {
      //this.setState({ constantDurationArray: this.state.constantDurationArray.push(duration) })
        this.setState(prevState => ({
         constantDurationArray: [...prevState.constantDurationArray, duration]
       }))
      } 
    } 
    if (timerType === 'constant') {
      this.setState({ constantInterval: intervals }, this.countdown)
    } else {
      this.setState({ constantInterval: intervals})
    }
  }

  setIntervalsForCustomBreak = () => {
    const { setIntervals } = this.props
    let intervals = parseFloat(document.getElementById('intervals').value)
    setIntervals(intervals)
  }

  toggleFirstVariableNext = () => {
    this.setState({ firstVariableNext: true })
  }

  getVariableIntervalTimes = () => {
    const { intervals } = this.props
    let constantDurationArrayPlaceHolder = []
    for(let i = 0; i < intervals; i++) {
      let duration = parseFloat(document.getElementById(`variable${i}`).value)
      constantDurationArrayPlaceHolder.push(duration)
    }
    this.setState({ constantDurationArray: constantDurationArrayPlaceHolder}, this.countdown)
  }

  getBreakInfo = () => {
   const { breaksType, intervals } = this.props
   let breaks
   if (breaksType === 'constant') {
    let breakTimes = parseFloat(document.getElementById('breakDuration').value)
    setBreakTime(breakTimes)
    breaks = []
    for (let i = 0; i < this.state.constantInterval; i++) {
      breaks.push(breakTimes);
    }
   } else {
    breaks = []
      for (let j = 0; j < intervals; j++) {
        let breakTimes = parseFloat(document.getElementById(`break${j}`).value)
        breaks.push(breakTimes)
      }
   }
    this.setState({ breaksArray: breaks})
    return breaks;
  }

  render() {
    const { viewType, 
            timerType, 
            constantDuration, 
            intervals, 
            setViewType, 
            setTimerType, 
            countdownStarted, 
            setCountdownStart, 
            breaksCondition, 
            setBreaksCondition,
            setBreaksType,
            breaksType}  = this.props

    let timeInput, 
        constantDoneButton, 
        constantTime, 
        constantTimeCountdown, 
        breakChoice, 
        breaksTypeComp, 
        breakInput, 
        variableInterval, 
        variableDoneButton,
        variableIntervalNext

    if (timerType) {
      timeInput = <TimeInput />
    }

    if (timerType === 'constant') {
      constantTime = <ConstantTime timerType={timerType}/>
      constantDoneButton = <ConstantDoneButton getConstantInfo={this.getConstantInfo} setCountdownStart={setCountdownStart}/>
      breakChoice = <BreakChoice setBreaksCondition={setBreaksCondition}/>
    }

    if (timerType === 'variable') {
      // getConstantInfo sets the number of intervals and in turn variableInterval gets called to list all of the duration
      // input selections, toggleFirstVariableNext tells the site that the first next button has been clicked and that the next button 
      // for entering variable interval durations can be shown
      variableDoneButton = <VariableDoneButton getVariableInfo={this.getConstantInfo} toggleFirstVariableNext={this.toggleFirstVariableNext}/>
      variableInterval = <VariableInterval intervals={intervals}/>
      breakChoice = <BreakChoice setBreaksCondition={setBreaksCondition}/>
    }

    if (this.state.firstVariableNext) {
      variableIntervalNext = <VariableIntervalNext getVariableIntervalTimes={this.getVariableIntervalTimes} setCountdownStart={setCountdownStart}/>
    }
    
    if (breaksCondition) {
      breaksTypeComp = <BreakType setBreaksType={setBreaksType} setIntervalsForCustomBreak={this.setIntervalsForCustomBreak}/>
    }

    if (countdownStarted) {
      constantTimeCountdown = <ConstantTimeCountdown  constantInterval={this.state.constantInterval} 
                                                      constantDurationState={this.state.constantDurationState} 
                                                      breakTime={this.breakTime} 
                                                      changeDisplay={this.changeDisplay}
                                                      breakRoundCounter={this.state.breakRoundCounter}
                                                      intervals={intervals} />
    }

    if (breaksType === 'constant') {
      breakInput = <BreakInput breaksType={breaksType} />
    } else if (breaksType === 'custom') { 
      breakInput = <BreaksList intervals={intervals}/>
    }

    return (
      <div>
        <SelectionScreen setTimerType={setTimerType} />
        {timeInput}
        
        {constantTime}
        
        {variableDoneButton}
        {variableInterval}
        
        {breakChoice}
        {breaksTypeComp}
        {breakInput}
        {constantDoneButton}

        {variableIntervalNext}
        {constantTimeCountdown}
      </div>
    )
  }

changeDisplay = () => {
  if (this.state.breakTime) {
    return "Seconds left for break"
  }
  return "Seconds left in current round"
}
countdown = () => {
  const { breaksCondition, constantDuration, breaksType } = this.props
  this.setState({ constantDurationState: this.state.constantDurationArray[this.state.constantDurationArray.length - this.state.constantInterval ]})
  var sound = new Audio(beep)
  let breaksArray
  if (document.getElementById('breakDuration') !== null || breaksType === 'custom') {
    breaksArray = this.getBreakInfo()
  }
  this.setState({ breakTime: false })
  
  console.log('here at countdown')
  let myInterval;
  myInterval = setInterval(() => {
    if (this.state.constantDurationState !== 0 && this.state.constantInterval >= 0) {
      this.setState({ constantDurationState: this.state.constantDurationState - 1 })  
    } else {
      if (this.state.constantInterval >= 0) {
        if (breaksCondition) {
          this.setState({ breakTime: true })
        }
        this.setState({ constantInterval: this.state.constantInterval - 1 })
        if (this.state.constantInterval !== 0) {
          sound.play()
        }
        if (breaksCondition) {
          clearInterval(myInterval)
          this.setState({ breakRoundCounter: breaksArray[breaksArray.length - this.state.constantInterval - 1]}, 
                          this.breakCountdown)
        } else {
          this.setState({ constantDurationState: this.state.constantDurationArray[this.state.constantDurationArray.length - this.state.constantInterval ]})
        }
      } 
    }if (this.state.constantInterval === 0) {
      if (!this.state.doneSoundPlayed) {
        var soundDone = new Audio(beepDone)
        soundDone.play()
        this.setState({ doneSoundPlayed: true })
      }
    }
  }, 1000)
  }

  breakCountdown = () => {
    const { constantDuration, breakTime } = this.props
    let breakInterval
     breakInterval = setInterval(() => {
      this.setState({ breakRoundCounter: this.state.breakRoundCounter - 1})
      if (this.state.breakRoundCounter === -1 && this.state.constantInterval >= 0) {
        var soundBreakDone = new Audio(beepBreakDone)
        soundBreakDone.play()
        clearInterval(breakInterval)
        this.setState({ constantDurationState: constantDuration}, this.countdown)
      }
    }, 1000)
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
