import { 
	CHANGE_VIEW_TYPE,
	CHANGE_TIMER_TYPE,
	CHANGE_CONSTANT_DURATION,
	CHANGE_INTERVALS,
	COUNTDOWN_STARTED,
	BREAKS_CONDITION,
	BREAKS_TYPE
} from './constants.js'

const viewType = {
	viewType: 'homePage'
}

const timerType = {
	timerType: ''
}

const constantDuration = {
	constantDuration: 0
}

const intervals = {
	intervals: 0
}

const countdownStarted = {
	countdownStarted: false
}

const breaksCondition = {
	breaksCondition: false
}

const breaksType = {
	breaksType: ''
}

const breakTime = {
	breakTime: false
}
export const updateViewType = (state=viewType, action={}) => {
	switch(action.type) {
		case CHANGE_VIEW_TYPE:
			return Object.assign({}, state, { viewType: action.payload })
		default:
			return state
	}
}

