import { 
	CHANGE_VIEW_TYPE,
	CHANGE_TIMER_TYPE,
	CHANGE_CONSTANT_DURATION,
	CHANGE_INTERVALS, 
	COUNTDOWN_STARTED,
	BREAKS_CONDITION,
	BREAKS_TYPE,
	BREAK_TIME
} from './constants.js'

export const setViewType = (viewType) => {
	return {
		type: CHANGE_VIEW_TYPE,
		payload: viewType
	}
}

export const setTimerType = (timerType) => {
	return {
		type: CHANGE_TIMER_TYPE,
		payload: timerType
	}
}

export const setConstantDuration = (duration) => {
	return {
		type: CHANGE_CONSTANT_DURATION,
		payload: duration
	}
}

export const setIntervals = (intervals) => {
	return {
		type: CHANGE_INTERVALS,
		payload: intervals
	}
}

export const setCountdownStart = (countdownStarted) => {
	return {
		type: COUNTDOWN_STARTED,
		payload: countdownStarted
	}
}

export const setBreaksCondition = (breaksCondition) => {
	return {
		type: BREAKS_CONDITION,
		payload: breaksCondition
	}
}

export const setBreaksType = (breakType) => {
	return {
		type: BREAKS_TYPE,
		payload: breakType
	}
}

export const setBreakTime = (breakType) => {
	return {
		type: BREAKS_TYPE,
		payload: breakType
	}
}






