import React from 'react';

const ConstantTime = ({ entries, timerType, index }) => {
	let idValue
	if (timerType === 'constant') {
		idValue = 'constantDuration'
	} else {
		idValue = `variable${index}`
	}
	return (
		<div className="input-group input-group-lg w-25 center mb3">
  			<div className="input-group-prepend">
    			<span className="input-group-text" id="inputGroup-sizing-lg">Enter Duration per Interval</span>
  			</div>
  			<input type="text" className="form-control" aria-label="Sizing example input" aria-describedby="inputGroup-sizing-lg" id={idValue}/>
		</div>
	)
}

export default ConstantTime
