import React from 'react';

const BreakChoice = ({ setBreaksCondition }) => {
	return (
		<div className="tc">
			<h2 style={{display: "inline"}}>Would you like any breaks?</h2>
			<button type="button" class="btn btn-secondary ml2" onClick={() => setBreaksCondition(true)}>Yes</button>
			<button type="button" class="btn btn-secondary ml3" onClick={() => setBreaksCondition(false)}>No</button>
		</div>
	)
}


export default BreakChoice