import React from 'react'

const BreakType = ({ setBreaksType, setIntervalsForCustomBreak }) => {
	return (
		<div className="tc mv2 ">
			<button type="button" class="btn btn-secondary" onClick={() => setBreaksType('constant')}>Consant Break Time</button>
			<button type="button" class="btn btn-secondary ml2" onClick={() => {setBreaksType('custom'); setIntervalsForCustomBreak()}}>Custom Break Time</button>
		</div>
	)
}

export default BreakType