import React from 'react'
import BreakInput from './BreakInput'

const BreaksList = ({ intervals }) => {
	console.log(intervals)
	let breakList = []
	for (let i = 0; i < intervals; i++) {
		breakList.push(<BreakInput breaksType='custom' index={i} key={i} />)
	}
	return (
		<div>
			{breakList}
		</div>
	)
}

export default BreaksList
